import Vue from 'vue';

Vue.config.productionTip = false;
Vue.config.silent = true;
import App from './App.vue';
import '@mdi/font/css/materialdesignicons.css';
import Vuetify from 'vuetify';
import 'vuetify/dist/vuetify.min.css';
import VueRouter from 'vue-router';
import routes from './routes';
import store from '@/store';
import VueNativeSock from 'vue-native-websocket';

if(process.env.NODE_ENV === 'development'){
	Vue.use(VueNativeSock, 'wss://meetup.inachi.eu:4430/ws', {
		reconnection: true, // (Boolean) whether to reconnect automatically (false)
		reconnectionAttempts: 500, // (Number) number of reconnection attempts before giving up (Infinity),
		reconnectionDelay: 1000 ,// (Number) how long to initially wait before attempting a new (1000)
		format: 'json',
	});
} else {
	Vue.use(VueNativeSock, 'wss://meetup.inachi.eu:4430/ws', {
		reconnection: true, // (Boolean) whether to reconnect automatically (false)
		reconnectionAttempts: 500, // (Number) number of reconnection attempts before giving up (Infinity),
		reconnectionDelay: 1000 ,// (Number) how long to initially wait before attempting a new (1000)
		format: 'json',
	});
}
// const wsURI = process.env.NODE_ENV === 'development' ? 'ws://localhost:9001' : '';





Vue.use(Vuetify);
Vue.use(VueRouter);

const router = new VueRouter({
	routes,
	mode: 'history'
});


new Vue({
	//@ts-ignore
	router,
	store,
	vuetify: new Vuetify({
		icons: {
			iconfont: 'mdi' // default - only for display purposes
		}
	}),
	render: (h): any => h(App)
}).$mount('#app');
