import { VuexModule, Module, Mutation, Action, getModule } from 'vuex-module-decorators';
import store from '@/store';
import { AlertsModule } from './alerts';
import { VkModule, vkApi } from '@/store/modules/vk';

let WebSocket = null;
const requests = {};
let id = 0;

export interface IWebsocketState {
}

@Module({ dynamic: true, store, name: 'websocket' })
class Websocket extends VuexModule implements IWebsocketState {
	_isConnected: boolean = false;
	_isAuth: boolean = false;
	_isUserRegistered: boolean = false;

	get isConnected() {
		return this._isConnected;
	}

	get isAuth() {
		return this._isAuth;
	}

	get userRegistered() {
		return this._isUserRegistered || localStorage.getItem('userRegistered');
	}

	@Mutation
	SET_IS_CONNECTED(val: boolean) {
		this._isConnected = val;
	}

	@Mutation
	SET_IS_AUTH(val: boolean) {
		this._isAuth = val;
	}

	@Mutation
	SET_USER_REGISTERED(val: boolean) {
		this._isUserRegistered = val;
		if (val) {
			localStorage.setItem('userRegistered', '1');
		} else {
			localStorage.removeItem('userRegistered');
		}
	}

	@Action({ rawError: true })
	Init(payload) {
		const { eventListener } = payload;

		eventListener.onopen = (data) => {
			this.context.commit('SET_IS_CONNECTED', true);
			WebSocket = data.target;
			this.auth();
		};

		eventListener.onclose = (data) => {
			this.context.commit('SET_IS_CONNECTED', false);
			this.context.commit('SET_IS_AUTH', false);
		};

		eventListener.onmessage = (message) => {

			const data = message.data;
			// const data = message.data;
			let response = undefined;
			if (typeof data === 'object') {
				response = data;
			} else if (typeof data === 'string' && data.charAt(0) === '{') {
				try {
					response = JSON.parse(data);
				} catch (e) {
					console.error(e);
					return false;
				}
			} else {
				console.log(data);
				return false;
			}


			if (response.hasOwnProperty('id')) {
				this.onServerResponseById(response);
			} else {
				this.onServerNotification(response);
			}
		};

		eventListener.onerror = (error) => {
			console.log('onerror', error);
			/*			if (this.isConnected) {
							console.log('onerror', error);
						} else {
							this.context.commit('SET_IS_CONNECTED', false);
							this.context.commit('SET_IS_AUTH', false);
						}*/
		};
	}

	@Action({ rawError: true })
	async auth() {
		const paramsForRegister = await VkModule.paramsUserRegister();
		console.log('paramsForRegister',paramsForRegister)
		const paramsForAuth = await VkModule.paramsUserAuth();
		console.log('paramsForAuth',paramsForAuth)

		// if (!this.userRegistered) {
		// 	const paramsForRegister = await VkModule.paramsUserRegister();
		// 	await this.callServer(paramsForRegister).catch(e => {
		// 		console.error(e);
		// 		return false;
		// 	});
		// 	this.context.commit('SET_USER_REGISTERED', true);
		// }
		//
		// if (this.userRegistered) {
		// 	const paramsForAuth = await VkModule.paramsUserAuth();
		// 	await this.callServer(paramsForAuth).catch(e => {
		// 		console.error(e);
		// 		this.context.commit('SET_IS_AUTH', false);
		// 		return false;
		// 	});
		// 	this.context.commit('SET_IS_AUTH', true);
		// } else {
		// 	console.log('WTF???????????');
		// }
	}

	@Action({ rawError: true })
	sendText(text) {
		if (this.isConnected && WebSocket) {
			WebSocket.send(text);
		}
	}

	@Action({ rawError: true })
	sendObject(obj) {
		if (this.isConnected && WebSocket) {
			WebSocket.sendObj(obj);
		}
	}

	@Action({ rawError: true })
	callServer(obj) {
		const { params } = obj;
		id = id + 1;
		obj['jsonrpc'] = '2.0';
		obj['id'] = id;
		return new Promise((resolve, reject) => {
			try {
				if (this.isConnected ) {
					WebSocket.sendObj(obj);
					requests[id] = { params, resolve, reject };
				} else {
					reject({ 'code': -1, 'message': 'WebSocket not auth' });
				}
			} catch (e) {
				reject(e);
			}
		});
	}

	@Action({ rawError: true })
	onServerResponseById(obj) {
		const {id, error, result} = obj
		const tempObj = requests[id];
		if(tempObj){
			try {
				if(result){
					tempObj['resolve'](result);
				} else if(error){
					tempObj['reject'](error);
				} else {
					console.error('wrong response from server', obj);
				}
				delete requests[id];
			} catch (e) {
				console.error(e);
			}
		} else {
			console.error('onServerResponseById wrong id', obj);
		}
	}

	@Action({ rawError: true })
	onServerNotification(obj) {
		console.log('onServerNotification', obj);
	}

}

export const WebsocketModule = getModule(Websocket);
