






































	import { Component,Vue,} from 'vue-property-decorator';
	import {mainColor} from '@/models/Constants'
	@Component
	export default class ChatBox extends Vue {

		mainColor = mainColor
		sendIcon = 'mdi-send'

		sendMessage(): void {
			this.sendIcon = this.sendIcon == 'mdi-microphone' ? 'mdi-send' : 'mdi-microphone';
		}
}
